body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden; }

.app {
  width: 100%;
  height: 100%;
  position: relative; }

.page-header, .editor-header { height: 2em }

.page-footer { height: 3em }

.splitter-wrapper {
  width: 100%;
  height: calc(100% - 5em);
  position: relative;
  overflow: hidden; }

.pane {
  overflow: hidden; }

.editor-pane {
  height: 100%;
}

